import React from "react";
import { Rate } from 'antd';
import { FrownOutlined, MehOutlined, SmileOutlined, QuestionCircleFilled } from '@ant-design/icons'

const customIcons = {
  1: <FrownOutlined />,
  2: <FrownOutlined />,
  3: <MehOutlined />,
  4: <SmileOutlined />,
  5: <SmileOutlined />,
};

export default class Rating extends React.Component {

render(){
    return(
        <>
    <Rate
      defaultValue={3}
      character={({ index }) => {
        return customIcons[index + 1];
      }}
    />
    <QuestionCircleFilled />
  </>
    )
}


}